import logo from './logo.svg';
import './App.css';
import {
    MDBNavbar,
    MDBNavbarBrand,
    MDBNavbarNav,
    MDBNavItem,
    MDBNavLink,
    MDBNavbarToggler,
    MDBCollapse,
    MDBMask,
    MDBRow,
    MDBCol,
    MDBBtn,
    MDBView,
    MDBContainer,
    MDBFormInline,
    MDBAnimation
  } from "mdbreact";
import { Navbar, Nav } from 'react-bootstrap';

function Header() {
  return (
    <div className="App">
     
        <MDBNavbar  color="primary-dark"
              dark
              expand="md"
              fixed="top"
              scrolling
              transparent
              >
        <MDBNavbarBrand href="#home">
      <img
        src= "./logo192.png"
        width="50"
        height="50"
        className=" align-top"
        alt="logo"
        
      />
      </MDBNavbarBrand>
        <MDBNavbarBrand href="#home">Deepesh Patel</MDBNavbarBrand>
    
        <Nav className="ml-auto ">
        <Nav.Link activeclassName="menu-active" className="nav-link active" href="/">Home</Nav.Link>
        <Nav.Link activeclassName="menu-active" className="nav-link active" href="/Projects">Projects</Nav.Link>
        <Nav.Link activeclassName="menu-active" className="nav-link active" href="/Contact">Contact</Nav.Link>

       
        </Nav>
    </MDBNavbar>
    
    <MDBMask className="d-flex justify-content-center align-items-center gradient">
          <MDBRow >
            
          <MDBCol >
              
                  <MDBAnimation type="fadeInRight" delay=".3s">
                    <img
                      src='./Deepesh.jpg'
                      alt=""
                      className="img-fluid"
                    />
                  </MDBAnimation>
                  <MDBCol
                  md="6"
                  className="white-text text-center text-md-left mt-xl-5 mb-5"
                >
                  Hello
                  </MDBCol>

                </MDBCol>
           </MDBRow>
          </MDBMask>
    </div>
  );
}

export default Header;